.ft-element-tag {
  color: #000;
  opacity: .65;
  background-color: #fc0;
  border-radius: 3px;
  padding: .25rem .5rem;
  font-size: .8rem;
  display: inline;
  position: relative;
  bottom: .35rem;
  left: .35rem;
}

@keyframes backround-scroll {
  0% {
    background-position: 100% 0;
  }

  100% {
    background-position: -100% 0;
  }
}

@keyframes text-rotation {
  0% {
    opacity: 0;
  }

  1% {
    opacity: 0;
    transform: translateY(-20px);
  }

  3.5% {
    opacity: 1;
    transform: translateY(0);
  }

  11% {
    opacity: 1;
    transform: translateY(0);
  }

  12.5% {
    opacity: 0;
    transform: translateY(20px);
  }

  20% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

